:root {
  --font-base:monospace ;
  --font-alt: monospace;
  --color-golden: #DCCA87;
  --color-black: #0C0C0C;
  --color-gray: #545454;
  --color-crimson: #F5EFDB;
  --color-grey: #63ACDF;
  --color-white: #FFFFFF;
}

.app__navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  font-family: monospace;
  font-size: 0.9rem;
  padding: 1rem 2rem;
  max-width: 120rem;
  margin: auto;
  width: 92%;
  background: transparent;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
}

.app__navbar-logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.app__navbar-title {
  font-size: 2rem;
  color: #545454;
}

@media (max-width: 576px) {
  .app__navbar-title {
    font-size: 1rem;
  }
}
.app__navbar-title:hover {
  color: var(--hover-sky-blue);
  cursor: pointer;
  letter-spacing: 1px;
}

.app__navbar-logo img {
  width: 10%;
  margin-right: 1rem;
}

.app__navbar-links {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}
@media (max-width: 991.98px) {
  .app__navbar-links {
    position: fixed;
    z-index: 2232;
    right: 0;
    top: 7rem;
    flex-direction: column;
    width: 100%;
    background: #202023;
    border: 1px solid #4F4F4F;
    text-align: right;
    align-items: normal;
  }
  .app__navbar-links.hidden {
    transform: translateX(100%);
    pointer-events: none;
  }
  .app__navbar-links.visible {
    transform-origin: right;
    transform: translateX(0px);
  }
  .app__navbar-links li {
    border-block: 1px solid #4F4F4F;
    padding: 2rem;
  }
}
@media (max-width: 576px) {
  .app__navbar-links {
    top: 4rem;
  }
}

.app__navbar-links li a {
  margin: 1rem;
  cursor: pointer;
  color: #d4d4d4;
  font-size: 1.3rem;
  text-align: start;
  text-decoration: none;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-family: "Source Sans Pro", sans-serif;
}

.app__navbar-links li a:hover, .app__navbar-links li a:focus {
  color: var(--hover-sky-blue);
  font-size: 2.3rem;
}

@media screen and (max-width: 650px) {
  .app__navbar {
    padding: 1rem;
  }
}
.openIcon {
  background-color: transparent;
  border: 0;
  display: none;
}
@media (max-width: 991.98px) {
  .openIcon {
    display: block;
  }
}

.openIcon span {
  display: block;
  background-color: #4f4f4f;
  height: 3px;
  width: 25px;
  margin-top: 5px;
  margin-bottom: 5px;
  position: relative;
  left: 0;
  opacity: 1;
  transition: all 0.35s ease-out;
  transform-origin: center left;
}

.openIcon span:nth-child(1) {
  transform: translate(0%, 0%) rotate(0deg);
}

.openIcon span:nth-child(2) {
  opacity: 1;
}

.openIcon span:nth-child(3) {
  transform: translate(0%, 0%) rotate(0deg);
}

.openIcon span:nth-child(1) {
  margin-top: 0.3em;
}

.openIcon:not(.open) span:nth-child(1) {
  transform: translate(15%, -33%) rotate(45deg);
}

.openIcon:not(.open) span:nth-child(2) {
  opacity: 0;
}

.openIcon:not(.open) span:nth-child(3) {
  transform: translate(15%, 33%) rotate(-45deg);
}/*# sourceMappingURL=Navbar.css.map */